body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 120px;
  font-weight: bold;
  line-height: 120px;
  height: 120px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 120px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 70%;
  margin: 0 auto;
}

.game-info {
  margin-left: 20px;
}
.game-info div {
  font-weight: 700;
}

@media only screen and (max-width : 550px) {
  .square {
    
    font-size: 80px;
    line-height: 80px;
    height: 80px;
    width: 80px;
  }
}
@media only screen and (max-width : 380px) {
  .square {
    
    font-size: 60px;
    line-height: 60px;
    height: 60px;
    width: 60px;
  }
}